.expandIcon {
    fill: #809cc3;
    height: 6px;
    transition: transform .2s ease-in-out;
    width: 6px;
    margin-right: 4px;
    // position: relative;
    // top: -1px;
    
}
.row {
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 100%;
    padding: 0 10px;
    &:hover {
        background: #f6f8fa;
    }
}
.rotate {
    transform: rotate(90deg);
}
.checkbox {
    margin-left: auto;
}

.placeholder {
    fill: #809cc3;
    height: 14px;
    width: 14px;
    margin-right: 4px;
}
.iconContainer {
    display: flex;
    align-items: center;
}
.listContainer {
    height: 300px;
    // width: 300px;
    min-width: 200px;
}

.loading {
    margin-right: 4px;
    fill: #00b831;
}
